<template >
  <panel title=" ">
    <Products-Chart></Products-Chart>
    <template v-slot:header-right>
      <router-link to="/product/add">
        <vs-button color="primary" type="filled"
          ></vs-button
        > </router-link
      >&nbsp;
      <router-link to="/product/order"
        ><vs-button color="primary" type="filled"
          >/</vs-button
        ></router-link
      >&nbsp;
      <router-link to="/product/withdrawReturn"
        ><vs-button color="primary" type="filled"
          >/</vs-button
        ></router-link
      >&nbsp;
      <router-link to="/product/sale"
        ><vs-button color="danger" type="filled"></vs-button></router-link
      >
    </template>

      <div class="vx-row">
        <div class="vx-col w-full md:w-1/1 lg:w-1/1 xl:w-1/1">
          <vs-row vs-type="flex" vs-justify="flex-end"
            ><div class="right"></div
          ></vs-row>

          <vs-table
            pagination
            max-items="10"
            search
            :data="products"
            title="danger"
          >
            <template slot="thead">
              <vs-th sort-key="id"></vs-th>
              <vs-th sort-key="product_code"></vs-th>
              <vs-th sort-key="name_th"> (TH)</vs-th>
              <vs-th sort-key="name_en"> (EN)</vs-th>
              <vs-th sort-key="description"></vs-th>
              <vs-th sort-key="quantity"></vs-th>
              <vs-th sort-key="wholesale_price"></vs-th>
              <vs-th sort-key="retail_price"></vs-th>
              <vs-th sort-key="retail_price"></vs-th>
              <vs-th sort-key="price"></vs-th>
            </template>
            <template slot-scope="{ data }">
              <vs-tr :key="product" v-for="(products, product) in data">
                <vs-td>
                  {{ product + 1 }}
                </vs-td>
                <vs-td>
                  {{ data[product].code }}
                </vs-td>
                <vs-td>
                  {{ data[product].setting_master_product.name_th }}
                </vs-td>
                <vs-td>
                  {{ data[product].setting_master_product.name_en }}
                </vs-td>
                <vs-td>
                  {{ data[product].description }}
                </vs-td>
                <vs-td>
                  {{ data[product].quantity }}
                </vs-td>
                <vs-td>
                  {{ data[product].wholesale_price }}
                </vs-td>
                <vs-td>
                  {{ data[product].retail_price }}
                </vs-td>
                <vs-td>
                  <vs-chip v-for="tag in data[product].tags" :key="tag.text">{{
                    tag.text
                  }}</vs-chip>
                </vs-td>
                <vs-td>
                  <div class="flex mb-3" align="center">
                    <div class="w-1/6 bg-grid-color h-12">
                      <vs-button
                        radius
                        color="primary"
                        type="flat"
                        icon-pack="feather"
                        icon="icon-search"
                        @click="infoData(products)"
                      ></vs-button>
                    </div>
                    <div class="w-1/6 bg-grid-color h-12">
                      <vs-button
                        radius
                        color="warning"
                        type="flat"
                        icon-pack="feather"
                        icon="icon-edit"
                        @click="editData(products)"
                      ></vs-button>
                    </div>
                    <div class="w-1/6 bg-grid-color h-12">
                      <vs-button
                        radius
                        color="danger"
                        @click="openConfirm(products)"
                        type="flat"
                        icon-pack="feather"
                        icon="icon-trash"
                      ></vs-button>
                    </div>

                    <div class="w-1/6 bg-grid-color-secondary h-12"></div>
                  </div>
                </vs-td>
              </vs-tr>
            </template>
          </vs-table>
        </div>
      </div>
<pre></pre>
  </panel>
</template>


<script>
import 'swiper/dist/css/swiper.min.css'
import ProductsChart from './ProductsChart'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  components: {
    ProductsChart
  },
  data () {
    return {
      activeConfirm: false,
      swiperOption: {
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      popupActive: false,
      products: [],
      productDelete: null
    }
  },
  comments: {
    swiper,
    swiperSlide
  },
  methods: {
    openConfirm (products) {
      this.productDelete = products
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: ' ?',
        acceptText: '',
        cancelText: '',
        text: ` ${this.productDelete.name}`,
        accept: this.acceptAlert
      })
    },
    async acceptAlert () {
      try {
        await this.$http.delete(`/api/product/${this.productDelete.id}`)
        await this.loadData()
      } catch (error) {
        // this.$swal('', '', 'error')
      }
      this.$vs.notify({
        color: 'success',
        title: '',
        text: ''
      })
    },
    async loadData () {
      try {
        const { data } = await this.$http.get('/api/product')
        this.products = data.data
      } catch (error) {
        throw error
      }
    },
    editData (products) {
      this.$router.push({ name: 'product_edit', query: { id: products.id } })
    },
    infoData (products) {
      this.$router.push({ name: 'product_info', query: { id: products.id } })
    }
  },
  mounted () {
    this.loadData()
  }
}
</script>

