<template>
  <div id="extra-component-chartist-demo">
    <div class="vx-row">
      <div class="vx-col md:w-1/1 w-full mb-base">
        <vx-card>
          <vue-apex-charts
            ref="testharts"
            type="bar"
            height="350"
            :options="option"
            :series="series"
          ></vue-apex-charts>
        </vx-card>
      </div>
    </div>
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'Products-Chart',
  data () {
    return {
      series: [
        {
          name: '',
          data: []
        }
      ],

      option: {
        colors: ['#7367F0', '#28C76F', '#EA5455', '#FF9F43', '#1E1E1E'],
        plotOptions: {
          bar: {
            horizontal: false,
            endingShape: 'rounded',
            columnWidth: '50%'
          }
        },
        dataLabels: {
          enabled: false
        },
        stroke: {
          show: true,
          width: 2,
          colors: ['transparent']
        },
        xaxis: {
          categories: [],
          convertedCatToNumeric: false
        },
        yaxis: {
          title: {
            text: '$ (thousands)'
          }
        },
        fill: {
          opacity: 1
        },
        tooltip: {
          y: {}
        }
      }
    }
  },
  methods: {
    async loadData () {
      try {
        const { data:{data:RESULT} } = await this.$http.get('/api/test_chart')
        for (const index in RESULT) {
          this.option.xaxis.categories.push(RESULT[index].name)
          this.series[0].data.push(RESULT[index].value)
        }
        this.$refs.testharts.refresh()
        console.log('================================')
      } catch (error) {
        throw error
      }
    }
  },
  mounted () {
    this.loadData()
  },
  components: {
    VueApexCharts
  }
}
</script>
